<template>
  <div>
    <div class="page-title">
      <h3>Отрядная статистика</h3>

      <button class="btn waves-effect waves-light btn-small" @click="refresh">
        <i class="material-icons">refresh</i>
      </button>
    </div>
    <div class="row">
      <p>Выберите период:</p>
      <select style="display: block" v-model="choosed">
        <option v-for="d of period" :key="d.id">{{ d.title }}</option>
      </select>
      <p>Выберите отряд:</p>
      <select style="display: block" v-model="tag">
        <option v-for="s of squads" :key="s">{{ s }}</option>
      </select>
      <Loader v-if="loading" />
      <v-table v-else :data="n">
        <thead slot="head">
          <th sortKey="id">Место</th>
          <v-th sortKey="playerName">Ник</v-th>
          <v-th sortKey="totalPlayedGames">Кол-во игр</v-th>
          <v-th sortKey="kills">Убийств</v-th>
          <v-th sortKey="teamkills">Тимкиллов</v-th>
          <v-th sortKey="deaths">Смертей</v-th>
          <v-th sortKey="kdRatio">K/D</v-th>
          <v-th sortKey="totalScore" defaultSort="desc">Счет</v-th>
        </thead>
        <tbody slot="body" slot-scope="{ displayData }">
          <tr v-for="(row, index) in displayData" :key="row.id">
            <td>{{ index + 1 }}</td>
            <td>
              <router-link
                :to="{ name: 'Player', params: { nick: row.playerName } }"
                >{{ row.lastSquadPrefix + row.playerName }}</router-link
              >
            </td>
            <td>{{ row.totalPlayedGames }}</td>
            <td>{{ row.kills }}</td>
            <td>{{ row.teamkills }}</td>
            <td>{{ row.deaths }}</td>
            <td>{{ row.kdRatio }}</td>
            <td>{{ row.totalScore }}</td>
          </tr>
        </tbody>
      </v-table>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data: () => ({
    n: [],
    tag: "[FNX]",
    squads: [],
    loading: true,
    choosed: "За все время",
    period: [
      {
        id: 1,
        title: "За все время",
      },
      { id: 2, title: "За ротацию от 27.02.2022" },
    ],
  }),
  async mounted() {
    this.loading = true;
    let s = await fetch("stats.json");
    s = await s.json();
    for (let key in s.squadStatistics) {
      this.squads.push(s.squadStatistics[key].prefix);
    }
    await this.r();
    this.loading = false;
  },
  watch: {
    tag: function (newt, old) {
      this.n = [];
      this.refresh();
    },
    choosed: function () {
      this.n = [];
      this.refresh();
    },
  },
  methods: {
    async refresh() {
      if (this.choosed == this.period[0].title) {
        this.r();
      } else if ((this.choosed = this.period[1].title)) {
        this.rotationTable();
      }
    },
    async r() {
      this.loading = true;
      this.n = [];
      let r = await fetch("stats.json");
      r = await r.json();
      let id = 1;
      for (let key in r.globalStatistics) {
        if (r.globalStatistics[key].lastSquadPrefix === this.tag) {
          r.globalStatistics[key].id = id;
          id++;
          this.n.push(r.globalStatistics[key]);
        }
      }
      this.loading = false;
    },
    async rotationTable() {
      this.loading = true;
      this.n = [];
      let r = await fetch("stats.json");
      r = await r.json();
      let id = 1;
      for (let key in r.globalStatistics) {
        let newPlayer = {};
        let newOne = [];
        let count = 0,
          kills = 0,
          teamkills = 0,
          deaths = 0;
        for (let i in r.globalStatistics[key].byWeeks) {
          if (r.globalStatistics[key].byWeeks[i].week >= "2022-10") {
            newOne.push(r.globalStatistics[key].byWeeks[i]);
            let m = r.globalStatistics[key].byWeeks[i];
            count += m.totalPlayedGames;
            kills += m.kills;
            teamkills += m.teamkills;
            deaths += m.deaths;
          }
        }
        if (count > 0 && r.globalStatistics[key].lastSquadPrefix == this.tag) {
          newPlayer.id = id;
          id++;
          newPlayer.totalPlayedGames = count;
          newPlayer.kills = kills;
          newPlayer.teamkills = teamkills;
          newPlayer.deaths = deaths;
          newPlayer.rotationWeeks = newOne;
          newPlayer.playerName = r.globalStatistics[key].playerName;
          newPlayer.lastSquadPrefix = r.globalStatistics[key].lastSquadPrefix;
          newPlayer.kdRatio = (kills / deaths).toFixed(2);
          newPlayer.totalScore = ((kills - teamkills) / count).toFixed(2);
          this.n.push(newPlayer);
        }
      }
      this.loading = false;
    },
  },
};
</script>
